import { useMemo } from "react";
import { TaskData, ChartData, StatsCalculations } from "./types";
import {
  calculateStats,
  calculateLabelStats,
  calculateSprintStats,
} from "./statsCalculations";

export const useTaskStats = (
  data: TaskData[],
  selectedTeam?: string | null
): StatsCalculations & { filteredData: TaskData[] } => {
  const filteredData = useMemo(() => {
    if (!selectedTeam) return data;
    return data.filter((task) => {
      const team = task[""]?.trim() || " ";
      return team === selectedTeam;
    });
  }, [data, selectedTeam]);

  const stats = useMemo(() => {
    const emptyStats: StatsCalculations = {
      taskTypeStats: [],
      taskPriorityStats: [],
      estimateStats: [],
      taskAssigneeStats: [],
      taskReporterStats: [],
      labelStats: [],
      sprintStats: [],
    };

    if (!filteredData.length) return emptyStats;

    return {
      taskTypeStats: calculateStats(filteredData, "", "count"),
      taskPriorityStats: calculateStats(filteredData, "", "count"),
      estimateStats: calculateStats(filteredData, "", "estimate"),
      taskAssigneeStats: calculateStats(filteredData, "", "count"),
      taskReporterStats: calculateStats(filteredData, "", "count"),
      labelStats: calculateLabelStats(filteredData),
      sprintStats: calculateSprintStats(filteredData),
    };
  }, [filteredData]);

  return {
    ...stats,
    filteredData,
  };
};
